import { useEffect } from 'react';
import { Route, BrowserRouter, Routes } from 'react-router-dom';
// import { AuthenticatedTemplate, useMsal, useMsalAuthentication } from '@azure/msal-react';
import { useMsal } from '@azure/msal-react';

import NoInternet from 'src/components/no-internet-dialog';
import useNetwork from 'src/hooks/useNetwork';
import Home from 'src/pages/home';
import NoMatch from 'src/pages/no-match';
// import { InteractionType } from '@azure/msal-browser';
import { useDispatch } from 'react-redux';
import DashboardLayout from 'src/components/layout';
import { ROUTES } from 'src/static/routes';
import { setNameAndEmailId } from 'src/redux/auth';
import TransferDocument from 'src/pages/transfer-document';
import Transport from './pages/transport-document';

function App() {
  const isOffline = useNetwork();
  const dispatch = useDispatch();
  const { accounts } = useMsal();

  // useMsalAuthentication(InteractionType.Redirect);

  useEffect(() => {
    dispatch(setNameAndEmailId(accounts));
  }, [accounts]);

  return (
    // <AuthenticatedTemplate>
    <BrowserRouter>
      <div>
        <DashboardLayout>
          <Routes>
            <Route exact path={ROUTES.dashboard} element={<Home />} />
            <Route exact path={ROUTES.transferDocument} element={<TransferDocument />} />
            <Route exact path={ROUTES.transportDocument} element={<Transport />} />
            <Route path='*' element={<NoMatch />} />
          </Routes>
        </DashboardLayout>
      </div>
      <NoInternet isOffline={isOffline} />
    </BrowserRouter>
    // </AuthenticatedTemplate >
  );
}

export default App;
